<template>
  <div class="modalitet">
      <div v-for="(i,index) in $store.state.items[ind]" class="mySlides fade">
        <div class="numbertext">{{index+1}} / {{$store.state.items[ind].length}}</div>
        <img :src="require('@/assets/'+i.src+'')" style="width:100%">
    </div>
    <a class="prev" @click="plusSlides(-1)">&#10094;</a>
    <a class="next" @click="plusSlides(1)">&#10095;</a>
  </div>
</template>

<script>
import eventBus from '../main'
  export default {
    data () {
      return {
        slideIndex : 1,
        ind:0
      }
    },
    // beforeMount(){
    //   eventBus.$on('open',ele=>{
    //         console.log('Opened before')
    //         this.ind=ele
    //         setTimeout(() => { 
    //           this.showSlides(this.slideIndex)  
    //         }, 500);
    //     })
    // },
    mounted(){  
        eventBus.$on('open',ele=>{
            this.ind=ele
            setTimeout(() => { 
              this.showSlides(this.slideIndex)  
            }, 500);
        })
    },
    methods:{
        currentSlide(n) {
            this.showSlides(this.slideIndex = n);
        },
        plusSlides(n) {
            this.showSlides(this.slideIndex += n);
        },
        showSlides(n){
        var i;
        var slides = document.getElementsByClassName("mySlides");
        if (n > slides.length) {this.slideIndex = 1}
        if (n < 1) {this.slideIndex = slides.length}
        for (i = 0; i < slides.length; i++) {
            slides[i].style.display = "none";
        }

        slides[this.slideIndex-1].style.display = "block";
        }
    }
  }
</script>
<style scoped>
*{box-sizing:border-box}

.modalitet{
    padding: 0%;
}
/* Slideshow container */
.slideshow-container {
  max-width: 1000px;
  position: relative;
  margin: auto;
}

/* Hide the images by default */
.mySlides {
  display: none;
}

/* Next & previous buttons */
.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}

/* Position the "next button" to the right */
.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover, .next:hover {
  background-color: rgba(0,0,0,0.8);
}

/* Caption text */
.text {
  color: #f2f2f2;
  font-size: 15px;
  padding: 8px 12px;
  position: absolute;
  bottom: 8px;
  width: 100%;
  text-align: center;
}

/* Number text (1/3 etc) */
.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

/* The dots/bullets/indicators */
.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active, .dot:hover {
  background-color: #717171;
}

/* Fading animation */
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}
</style>