<template>
  <v-container class="part">
        <div style="text-align:left;">
            <h2>Hi, i'm </h2>
            <h1>Stefan!</h1>
            <p>Welcome to my Portfolio Site.</p>
        </div>
        <br>
        <v-row v-if='$store.state.logged==true' justify='center'>
            <v-col cols='1'>
                <i class="fa fa-unlock"></i>
            </v-col>
            <v-col cols='4'>
                <h3>ACCESS GRANTED!</h3>
            </v-col>
        </v-row>
        <v-row v-else justify='center' >
            <p>Please wait user detection...</p>
            <v-col cols='2'>
                <i class="fa fa-lock"></i>
            </v-col>
            <v-col cols='7'>
                <v-progress-linear height=32 color="orange" buffer-value="0" :value="$store.state.loader" stream></v-progress-linear>
            </v-col>
            <v-col cols='3'>
                <h3>DETECTING USER...</h3>
            </v-col>
        </v-row>

  </v-container>
</template>
<script>
export default {
    created(){
        if(this.$store.state.loader!=100){
            const vm =this;
            const i = setInterval(() => {
                vm.$store.state.loader++;
                if(vm.$store.state.loader==100){
                    localStorage.setItem('logged',true)
                    vm.$store.state.logged=true
                    clearInterval(i)
                }
            }, 50);
        }
    }
}
</script>

<style scoped>
.part{
    margin-top: 100px;
    padding: 30px;
    background:black;
    text-align: center;
    border: orange solid 0.5px;
    /* border-radius: 20px; */
}
h3{
    color: orange;
}
i{
    color: orange;
    font-size:34px;
}
.lock{
    background-color: white;
}
.readBtn{
    background-color: rgba(255, 255, 255, 0.027);
    border: 0.5px solid orangered;
    color: orangered;
    padding: 5px;
    border-radius: 5px;
}
p{
    color:white;
    font-family: 'Inconsolata', monospace;
    font-size: 20px;
}
h1{
    color:white;
    font-family: 'Inconsolata', monospace;
    font-size: 50px;
}
h4,h2{
    color:white;
    font-family: 'Inconsolata', monospace;
}
</style>