<template>
  <form class="contact-form kartica" @submit.prevent="sendEmail" >
        <div align='center'>
            <h1>Contact me!</h1>
        </div>
        <input type="text" placeholder="Subject" v-model="$store.state.subject" class="area">
        <textarea v-model="$store.state.text" placeholder="Type message" cols='10' rows='5' class="area"></textarea>
        <v-row >
            <v-col md='6' sm='6' align='center'>
                <i class="fa fa-trash" id='del' @click="del"></i>
            </v-col>
            <v-col md='6' sm='6' align='center'>
                <a :href='href_string' target="_blank">
                    <i class="fa fa-paper-plane" id='sent' @click="send"></i>
                </a>
            </v-col>
        </v-row>
  </form>
</template>
<script>
import emailjs from 'emailjs-com';
export default {
    data(){
        return{
            emailRules: [
            v => !!v || 'E-mail is required',
            v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
            ]
        }
    },
    methods:{
        sendEmail: (e) => {
            emailjs.sendForm('gmail', 'template_eZ4xW61J', e.target, 'user_tvB5F5vxt6EjlL53SKBAw')
                .then((result) => {
                    console.log('SUCCESS!', result.status, result.text);
                }, (error) => {
                    console.log('FAILED...', error);
            });
        },
        remove(){
            this.$store.state.from=''
            this.$store.state.subject=''
            this.$store.state.text=''
            
        },
        del(){
            this.remove()
            document.getElementById('del').classList.add('animated')
            document.getElementById('del').classList.add('pulse')
            setTimeout(() => {
                document.getElementById('del').classList.remove('animated')
                document.getElementById('del').classList.remove('pulse') 
            }, 1000);
            this.flipMsg()
        },
        send(){
            document.getElementById('sent').classList.add('animated')
            document.getElementById('sent').classList.add('pulse')
            setTimeout(() => {
                document.getElementById('sent').classList.remove('animated')
                document.getElementById('sent').classList.remove('pulse') 
            }, 1000);
            this.flipMsg()
            setTimeout(() => {
                this.remove()
            }, 500);
        },
        flipMsg(){
            document.getElementById('msg').classList.add('animated')
            document.getElementById('msg').classList.add('flipInX')
            setTimeout(() => {
                document.getElementById('msg').classList.remove('animated')
                document.getElementById('msg').classList.remove('flipInX')    
            }, 1000);
        }
    }
    ,computed:{
        href_string(){
            return 'mailto:stefanneskovic2402@gmail.com?subject='+this.$store.state.subject+'&body='+this.$store.state.text
        } 
    }
}
</script>

<style scoped>
.row{
    margin-top: 20px;
}
.area{
    -webkit-box-shadow: 0px -1px 9px 0px rgba(255, 255, 255, 0.285);
    -moz-box-shadow: 0px -1px 9px 0px rgba(255, 255, 255, 0.285);
    box-shadow: 0px -1px 9px 0px rgba(255, 255, 255, 0.285);
    color:white;
    width: 100%;
    font-family: 'Inconsolata', monospace;
    border-radius: 5px;
    padding: 10px;
    margin-top: 50px;
    outline: none;
    background-color: rgba(255, 255, 255, 0.15);
}
i{
    font-size: 40px;
}
.fromField{
    background-color: rgba(255, 255, 255, 0.285);
    border-radius: 30px;
    padding-left: 10px;
    padding-right: 10px;
    width: 70%;
    color:white;
    font-family: 'Inconsolata', monospace;

}
.kartica{
    background-color: rgba(255, 255, 255, 0.1);
    padding: 30px;
    height: 100%;
    width: 95%;
    margin-left: 20px;
    color:white;
    font-family: 'Inconsolata', monospace;
}

</style>