<template>
  <div class="cmd">
         <v-row class="headercmd">
             <v-col class="cmdName"> 
                Command Prompt
             </v-col>
             <v-col cols='1'>
                 <i class="fa fa-window-minimize"></i>
             </v-col>
             <v-col cols='1'>
                 <i class="fa fa-square"></i>
             </v-col>
             <v-col cols='1' class="lastelement">
                 <i class="fa fa-times"></i>
             </v-col>
         </v-row>
     <div class="bodycmd">
        <div id='def'>
            <p>Microsoft Windows [Version 10.0.18362.778]</p>
            <p>(c) 2019 Microsoft Corporation. All rights reserved.</p>
            <p >C:\Users\Stefan> {{cdAbout}}</p>
        </div>
        <p v-if="cdAbout=='cd About'">C:\Users\Stefan\About> {{pyAbout}}</p>
        <div id="about" v-if="pyAbout=='python About.py'">
            <p>Hi! My name is Stefan Neskovic!</p>
            <p>I'm Full Stack Developer!</p>
            <p>22 years old</p>
            <p>Ljubovija, Serbia</p>
        </div>
        <p v-if="pyAbout=='python About.py'">C:\Users\Stefan\About> {{back}}</p>
        <p v-if="back=='cd..'">C:\Users\Stefan> {{cdSkills}}</p>
        <p v-if="cdSkills=='cd Skills'">C:\Users\Stefan\Skills> {{dir}}</p>
        <p v-for="(i,index) in skills" v-if="dir=='dir'" id="skill">{{index+10}}/05/2020  21:2{{index}}  {{i}}</p>
        <p v-if="dir=='dir'" id='point'>C:\Users\Stefan> <b>{{point==true ? ' ': '_' }}</b></p>
        <br>
     </div>
  </div>
</template>
<script>
export default {
    data(){
        return{
            point:true,
            cdAbout:'',
            cdSkills:'',
            back:'',
            dir:'',
            pyAbout:'',
            skills:[
                'Django.py',
                'Flask.py',
                'Vue.js',
                'AndroidStudio.java',
                'AdobeXD.xd',
                'Gimp.png',
                '<DIR> VC Git'
            ]
        }
    },
    created(){
        setTimeout(() => {
            this.writeCdAbout()    
        }, 50);
        setInterval(() => {
            this.point=!this.point            
        }, 500);
    },
    methods:{
        writeCdAbout(){
            // document.getElementById('def').hidden=false
            var cdAb = 'cd About'
            var brojac=0;
            var i=setInterval(() => {
                if(this.cdAbout=='cd About'){
                    this.writePyAbout()
                    clearInterval(i)
                }else{
                    this.cdAbout=this.cdAbout+cdAb[brojac]
                    brojac++;
                }
            }, 100);
        },
        writePyAbout(){
            var pyAb = 'python About.py'
            var brojac=0;
            var i=setInterval(() => {
                if(this.pyAbout==pyAb){
                    this.writeBack()
                    clearInterval(i)
                }else{
                    this.pyAbout=this.pyAbout+pyAb[brojac]
                    brojac++;
                }
            }, 100);
        },
        writeBack(){
            var back = 'cd..'
            var brojac=0;
            var i=setInterval(() => {
                if(this.back==back){
                    this.writeCdSkills()
                    clearInterval(i)
                }else{
                    this.back=this.back+back[brojac]
                    brojac++;
                }
            }, 100);
        },
        writeCdSkills(){
            var cdSkills = 'cd Skills'
            var brojac=0;
            var i=setInterval(() => {
                if(this.cdSkills==cdSkills){
                    this.writeDir()
                    clearInterval(i)
                }else{
                    this.cdSkills=this.cdSkills+cdSkills[brojac]
                    brojac++;
                }
            }, 100);
        },
        writeDir(){
            var dir = 'dir'
            var brojac=0;
            var i=setInterval(() => {
                if(this.dir==dir){
                    clearInterval(i)
                }else{
                    this.dir=this.dir+dir[brojac]
                    brojac++;
                }
            }, 100);
        }
    }
}
</script>

<style>
#skill, #about{
    color: yellow;
    line-height: 0.3;
}
.cmdName{
    padding-top: 15px;
    padding-bottom: 0%;
}
.cmd{
    -webkit-box-shadow: 0px -1px 15px 0px rgba(0, 0, 0, 0.285);
    -moz-box-shadow: 0px -1px 15px 0px rgba(0, 0, 0, 0.285);
    box-shadow: 0px -1px 15px 0px rgba(0, 0, 0, 0.285);
    /* border: 0.5px solid black; */
    padding: 0px;
    width: 100%;
    text-align: left;
    border-radius: 5px;
}
p{
    word-spacing: 0%;
}
.headercmd{
    line-height: 0;
    /* padding-top: 0%; */
    height: 30px;
    justify-items: center;
    padding-left: 20px;
    padding-right: 20px;
}
.bodycmd{
    overflow-y: scroll;
    height: 550px;
    margin: 0 auto;
    padding-left: 5px;
    background-color: black;
    font-family: 'Inconsolata', monospace;
    color:white;
}
</style>