<template>
  <div class=" gallery text-center">
    <v-dialog
      v-model="dialog"
      width='80%'
    >
      <template v-slot:activator="{ on }">
        <div id='gall'>
            <h1>Latest projects:</h1>
            <br>
            <br>
        <v-container>
        <v-row v-on="on" align="center">
                <v-col cols='12' md='4' sm='12' class='project' v-for="i,index in lista">
                    <Card :image='i' @click="dialog=true" v-on:click.native="openDialog(index)"  @mouseenter="ent(index)" @mouseleave="leav(index)" />
                </v-col>
                
            </v-row>
            <v-row>
                <v-col cols='12' md='4' sm='12'>
                    <img src='../assets/itd.png' width='100%'/>
                </v-col>
            </v-row>
        </v-container>
        </div>           
      </template>

      <v-card>
        <ViewSite/>
      </v-card>
    </v-dialog>
  </div>
</template>


<script>
import Card from '../components/GalleryCard'
import ViewSite from '../components/ViewSite'
import eventBus from '../main'
export default {
    components:{
        Card,
        ViewSite
    },
    data(){
        return{
            dialog:false,
            lista:[
                {
                    img:'cpEpijaca.png',
                    name:'Epijaca platform'
                },
                {
                    img:'cpCovid.png',
                    name:'Covid platform'
                },
                {
                    img:'cpMenjacnica.png',
                    name:'Exchange Office site'
                }
            ]
        }
    },
    methods:{
        ent(index){
            document.getElementsByClassName('kartica')[index].classList.add('pulse')
        },
        leav(index){
            document.getElementsByClassName('kartica')[index].classList.remove('pulse')
        },
        openDialog(index){
            eventBus.$emit('open',index)
            
        }
    }
}
</script>
<style scoped>
.project{
    padding: 20px;
}
.dail{
    padding: 0px;
    width: 100%;
}
#gall{
    width: 100%;
    background-color: white;
    /* height: 100vh; */
    padding-top: 50px;
    margin-bottom: 50px;

}
h1{ 
    font-family: 'Inconsolata', monospace;
}
</style>