<template>
    <div class="glavna">
        <v-container id='skills'>
                <v-row>
                    <v-col v-for="(i,index) in $store.state.lista" class="redovi" cols='12' md='4' sm='12'>
                        <Job :value='i' @mouseleave.native="leaveMouse(index)" @mouseenter.native="enterMouse(index)"/>
                    </v-col>
                </v-row>
        </v-container>
    </div>
</template>
<script>
import Job from '../components/JobCard'
export default {
    components:{
        Job
    },
    methods:{
        enterMouse(index){
            document.getElementsByClassName('kartic')[index].style.border='5px solid orange'
        },
        leaveMouse(index){
            document.getElementsByClassName('kartic')[index].style.border='none'
        }
    }
}
</script>
<style scoped>
.glavna{
    /* height: 100vh; */
    width: 100%;
    background-color: black;
}
.row{
    margin: 0;
}
#skills{
    margin-top: 100px;
    margin-bottom: 100px;
    width: 100%;
    background: black;
    background: radial-gradient(circle, orange 20%, black 80%);
    padding: 0px;
}
.redovi{
    padding: 0.5px;
}
</style>