<template>
    <div id="msg">
        <div class="kartica"  v-if="$store.state.subject!='' && $store.state.text!=''">
            <div align='right'>
                <i class="fa fa-pen-fancy"></i>
            </div>
            <h1>{{$store.state.subject}}</h1>
            <hr>
            <br>
            <h5>{{$store.state.text}}</h5>
            <br>
            <v-row class="send">
                <v-col cols='12' md='6' sm='6'></v-col>
                <v-col cols='12' md='6' sm='6'>
                    <p>{{date}}</p>
                </v-col>
                
            </v-row>
        </div>
        <div class="kartica" align='center' v-else>
            <h5>Waiting for your message...</h5>
        </div>
    </div>
</template>
<script>
export default {
    computed:{
        date(){
            var datum = new Date() 
            return datum.getDate()+'.'+datum.getMonth()+'.'+datum.getFullYear()
        }
    }
}
</script>

<style scoped>
#msg{
    background-color: rgba(255, 255, 255, 0.1);
    height: 100%;
    padding: 10px;
    width: 95%;
    margin-left: 20px;
}
p{
    font-size: 30px;
}
h5{
    font-size: 25px;
}
h1{
    font-size: 50px;
}
.kartica{
    /* background-color: rgba(255, 255, 255, 0.1); */
    height: 100%;
    border: 0.5px solid white;
    padding: 30px;
    color:white;
    font-family: 'Reenie Beanie', cursive;
}
</style>