<template>
    <div>
        <cv/>
        <img src="../assets/izrezano.png">
        <v-row>
            <v-col  md='3'  id='markers'>
                <i class=" mob fa fa-mobile" @click="mobClick" id='mob'></i>
            </v-col>
            <v-col md='3' id='markers'>
                <i class=" email fa fa-envelope-square" @click="emailClick"  id='email'></i>
            </v-col>
            <v-col md='3' id='markers'>
                <i class="git fa fa-github" @click="gitClick" id='git'></i>
            </v-col>
            <v-col md='3' id='markers'>
                <i class="link fa fa-link" @click="linkClick" id='link'></i>
            </v-col>
        </v-row>
        <v-row >
            <v-col cols="3" id='markers'>
                <Mark v-if='ele==1'/>
            </v-col>
            <v-col cols="3" id='markers'>
                <Mark v-if='ele==2'/>
            </v-col>
            <v-col cols="3" id='markers'>
                <Mark v-if='ele==3'/>
            </v-col>
            <v-col cols="3" id='markers'>
                <Mark v-if='ele==4'/>
            </v-col>
        </v-row>
        <v-card>
            <v-row>
                <v-col cols='2' style='background-color:grey;'>
                    <i class="ele fa fa-link"></i>
                </v-col>
                <v-col cols='10'>
                    <p>{{text}}</p>
                </v-col>
            </v-row>
        </v-card>
    </div> 
</template>
<script>
import Mark from './Marker'
import cv from './CV'
import { mdiFileAccount } from '@mdi/js';
export default {
    data(){
        return{
            text:'0616202600',
            ele:1
        }
    },
    components:{
        Mark,
        cv
    },
    methods:{
        mobClick(){
            this.text='061202600'
            this.ele=1
            document.getElementById('mob').classList.add('animated')
            document.getElementById('mob').classList.add('pulse')
            setTimeout(() => {
                document.getElementById('mob').classList.remove('animated')
                document.getElementById('mob').classList.remove('pulse') 
            }, 1000);
        },
        emailClick(){
            this.text='stefanneskovic2402@gmail.com'
            this.ele=2
            document.getElementById('email').classList.add('animated')
            document.getElementById('email').classList.add('pulse')
            setTimeout(() => {
                document.getElementById('email').classList.remove('animated')
                document.getElementById('email').classList.remove('pulse') 
            }, 1000);
        },
        gitClick(){
            this.text='github.com/NeskovicStefan2402'
            this.ele=3
            document.getElementById('git').classList.add('animated')
            document.getElementById('git').classList.add('pulse')
            setTimeout(() => {
                document.getElementById('git').classList.remove('animated')
                document.getElementById('git').classList.remove('pulse') 
            }, 1000);
        },
        linkClick(){
            this.text='elab.fon.bg.ac.rs/stefan-neskovic/'
            this.ele=4
            document.getElementById('link').classList.add('animated')
            document.getElementById('link').classList.add('pulse')
            setTimeout(() => {
                document.getElementById('link').classList.remove('animated')
                document.getElementById('link').classList.remove('pulse') 
            }, 1000);
        }
    }
}
</script>

<style scoped>
img{
  max-width: 60%;
  border-radius: 50%;
  
}
#markers{
    margin-bottom: 0;
    padding-bottom: 0;
}
.mob{
    color: blue;
    font-size:50px;
}
.email{
    color: grey;
    font-size:40px;
    padding-top:5px;
}
.git{
    padding-top:5px;
    font-size: 40px;
}
.link{
    padding-top:10px;
    font-size: 30px;
}
.ele{
    color: white;
}
</style>